
.h5 {
    color: rgb(176, 78, 78);
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 2px,
    
}

.h5-light{
    color: #333333;
    margin-left: 20px;
}

/* Modo light end */

.filter-container-light{
    overflow: hidden;
    padding-top: 15px;
    transition-duration: 01s;
    background-color: rgb(224, 224, 224);
}

.container-light{
    height: 100vh;
    padding-top: 20px;
    transition-duration: 01s;
    background-color: rgb(224, 224, 224);
    color: #333333;
}

.filter-box-light{
    margin: 10px;
    margin-top: 60px;
    transition-duration: 01s;
    background-color: rgb(224, 224, 224);
}

.select-filter-light{
    margin: 7px;
    padding: 4px;
    border-radius: 5px;
    border: none;
    transition-duration: 01s;
    color: #333333;
    background-color: rgb(224, 224, 224);
    box-shadow: -5px -5px 10px rgba(94, 104, 121, 0.486), 5px 3px 8px rgb(255, 255, 255);
}

.button-reset-filter-light{
    cursor: pointer;
    margin: 7px;
    padding: 4px;
    border-radius: 5px;
    border: none;
    transition-duration: 01s;
    background-color: rgb(224, 224, 224);
    color: #333333;
    box-shadow: -5px -5px 10px rgba(94, 104, 121, 0.486), 5px 3px 8px rgb(255, 255, 255);
}

.button-reset-filter-light:hover{
    transition-duration: 01s;
    color: rgba(141, 13, 13, 0.767);
    box-shadow: -3px -2px 5px rgba(255, 0, 0, 0.384), 3px 2px 5px rgba(253, 99, 99, 0.1);
}


.button-light{
    margin: 3.2rem;
    padding: .5rem;
    border-radius: 5px;
    border: none;
    transition-duration: 01s;
    color: #333333;
    background-color: rgb(224, 224, 224);
    box-shadow: -5px -5px 10px rgba(94, 104, 121, 0.486), 5px 3px 8px rgb(255, 255, 255);
}

.button-light:hover {
    background-color:rgba(216, 95, 95, 0.479);
}

/* Modo light fin */

