.face {
    color: #fff;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-wrap: wrap;
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.face.front {
    z-index: 20;
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.face.front::before {
    position: absolute;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    content: "";
    background: linear-gradient(45deg, rgba(0, 0, 0, 0.95) 0%, rgba(0, 0, 0, 0.5) 40%, rgba(0, 0, 0, 0.1) 100%) no-repeat;
    opacity: 0.7;
    z-index: 20;
    transition: opacity 0.2s ease;
}

.face.front::after {
    position: absolute;
    top: 7%;
    right: 7%;
    bottom: 7%;
    left: 7%;
    border: 1px solid rgba(255, 255, 255, 0.35);
    z-index: 100;
    content: "";
}

.face.front .title-wrapper {
    position: absolute;
    bottom: 3%;
    left: 20%;
    right: 20%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-wrap: wrap;
    z-index: 100;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: -webkit-transform 0.28s ease;
    transition: transform 0.28s ease;
    transition: transform 0.28s ease, -webkit-transform 0.28s ease;
}

.face.front .title-wrapper .title-gas {
    position: relative;
    font-family: 'Archivo Black', sans-serif;
    width: 100%;
    text-transform: uppercase;
    /* font-size: 1.8vw; */
    line-height: 1;
    opacity: 0.8;
    transition: opacity 0.2s ease;
}

.face.front .title-wrapper .title-gas::after {
    display: block;
    margin: 7% auto 8% auto;
    width: 100%;
    height: 1px;
    content: "";
    background-color: #fff;
    opacity: 0.5;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: -webkit-transform 0.15s;
    transition: transform 0.15s;
    transition: transform 0.15s, -webkit-transform 0.15s;
}

.face.front .title-wrapper .subtitle {
    /* font-size: 0.9vw; */
    line-height: 1;
    letter-spacing: 1px;
    text-transform: uppercase;
    opacity: 0;
    transition: opacity 0.15s;
}

.face.front:hover::before {
    opacity: 0.9;
}

.face.front:hover .title-wrapper {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.face.front:hover .title-wrapper .title-gas {
    opacity: 1;
}

.face.front:hover .title-wrapper .title-gas::after {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.5;
    transition-delay: 0.15s;
    transition-duration: 0.2s;
}

.face.front:hover .title-wrapper .subtitle {
    opacity: 0.7;
    transition-delay: 0.2s;
    transition-duration: 0.2s;
}

.face.back {
    color: #222;
    background: #fff;
    z-index: 10;
}

